@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body{
    background-color: #e7e7e7;
}

* {
    box-sizing: border-box;
}

.flex-container {
    /* We first create a flex layout context */
    display: flex;
}
.header-line{
    height:55px;
    width:100%;

    background-color: #85bfd0;
    border-bottom: 1px solid #6c99a7;

    font-family: 'Bebas Neue', cursive;
    font-size:40px;
    font-weight: 600;
    color: #ffffff;
    letter-spacing: 2px;
    /* text stroke */
    /*-webkit-text-stroke: 1px black;*/
    /*paint-order: stroke fill;*/
    text-shadow:
            -1px -1px 0 #000,
            0   -1px 0 #000,
            1px -1px 0 #000,
            1px  0   0 #000,
            1px  1px 0 #000,
            0    1px 0 #000,
            -1px  1px 0 #000,
            -1px  0   0 #000;

    vertical-align: middle;
}

.title-text{
    display:inline-block;
    position:relative;
    left:15px;
    top:7px;
}

.lock-icon{
    font-size:30px;
    display:inline-block;
    position:relative;
    left:7px;
    top:7px;
}

.side-icons{
    font-size:28px;
    display:inline-block;
    position:relative;
    top:14px;
    right:15px;
    float:right;
}

.night-mode:hover{
    background-color: #e65525;
    border-radius:50%;
    box-shadow: 0 0 0 3px #e78267;
}

.facebook-link:hover{
    background-color: #e65525;
    border-radius:50%;
    box-shadow: 0 0 0 3px #e78267;
}

span.space{
    margin:5px;
}

@media (max-width:400px) {
    .lock-icon{
        display:none;
    }
}

@media (max-width:360px) {
    .side-icons {
        display:none;
    }

    .title-text{
        word-break: keep-all;
    }
}





.side-bar{
    border-right:1px solid #dbdbdb;
    background-color: #f5f5f5;
    height:80vh;
    width:300px;
    font-size:17px;
    white-space: nowrap;
}

@media (max-width:600px) {
    .side-bar{
        display:none;
    }
}

.up-nav{
    border-bottom:1px solid #dbdbdb;
    background-color: #f5f5f5;
    white-space: nowrap;
    font-size:17px;
}


@media (min-width:600px){
    .up-nav{
        display:none;
    }
}

.content-table{
    background-color: #f5f5f5;
    padding:10px;
    border:1px solid #dbdbdb;
    border-radius:10px;
    margin:10px 10px 10px 10px;
}

.confession-box-title{
    color: #4a5465;
    text-align: center;
}

.title{
    margin: 13px 5px 5px;
    position:relative;
    bottom:3px;
    display:inline-block;
    font-size: 25px;
}

.switch-text{
    font-size:14px;
    width:100px;
    text-align:center;
    color: #323842;
}

.text-field{
    margin:10px;
}

.main-table{
    /* Center vertically */
    padding:10px;
    width:100vw;
}



.list-item-title{
    margin: 10px 10px 10px 10px;
}

.emergency-link a{
    margin:5px;
    color: #7f7f7f;
    text-decoration: none;
}


.emergency-link a:hover{
    transition: 0.3s;
    border-bottom: 3px solid #85bfd0;
}

.footer-main{
    background-color: #efefef;
    border-radius: 10px;
    border-top:1px solid #dbdbdb;
    border-bottom: 1px solid #dbdbdb;

    padding:15px;
    text-align: center;
}

.text-width{
    margin:0 auto;
    width:75vw;
}
.huji-text{
    color: #8d8d8d;
    font-size:14px;

}

.admin-text{
    color: #acacac;
    font-size:12px;
}

.rights-text{
    color: #8d8d8d;
    font-size:15px;
}

.huji-text a{
    color: #6896b4;
    font-weight: 700;
}
